$scheme-elements-transitions:() !global;

/// Ajout d'une combinaison de couleur sur un Ã©lÃ©ment pour un propriÃ©tÃ© donnÃ©e
/// @access private
/// @param {String} $set-name - nom de la combinaison comme dÃ©fini dans settings/sets
/// @@param {String} $prop - propriÃ©tÃ© affectÃ©e
/// @param {Bool | String} $transition - objet auqel la transition de couleur est associÃ©e
/// @param {Bool | String} $hover - si true, un effet de hover est appliquÃ© sur l'Ã©lÃ©ment en correspondance avec la couleur. la valeur override permet de simplement changer la couleur de hover (si un hover a dÃ©ja Ã©tÃ© appliquÃ© sur l'Ã©lÃ©ment)
/// @param {String | list}  $value - la valeur appliquÃ©e Ã  la propriÃ©tÃ©. la variable $COLOR est remplacÃ©e par la custom property de la combinaison de couleur
/// @param {String}  $element - le sÃ©lÃ©cteur de l'Ã©lÃ©ment

@mixin _scheme-element($set-name, $prop, $transition:false, $hover:false, $value:$COLOR, $element:&) {
  // hover
  @if $hover != false {
    $set: map_get($scheme-sets, $set-name);
    $light: _scheme-set-color($set, light);
    $dark: _scheme-set-color($set, dark);

    @at-root {
      @if $hover != override {
        #{$element} {
          @include _scheme-element-hover();
        }
      }

      #{$not-dark-theme-selector} {
        #{$element} {
          @include _get-hover-vars($light);
        }

        $i: 0;
        @each $type, $variation in $scheme-blocks {
          $i: ($i + 1);

          [class*="#{$base-scheme}-#{$type}"],
          [class*="--scheme-#{$type}-"] {
            #{$element} {
              @include _get-hover-vars(_scheme-set-color($set, $type));
            }
          }
        }
      }

      #{$dark-theme-selector} #{$element} {
        @include _get-hover-vars($dark);
      }
    }
  }

  // le $COLOR de la valeur est remplacÃ© par la custom property de la couleur
  #{$prop}: _replace_color($value, var(--#{$set-name}), false);

  // transitions
  @if $transition != false and $theme-allow-transition != false {
    @if $transition == true {
      $transition: $element;
    }

    $transitions:map_get($scheme-elements-transitions, $transition);
    @if $transitions == null {
      $transitions:();
    }

    $transitions:append($transitions, $prop $theme-transition-duration, $separator: comma);
    $scheme-elements-transitions:map_merge($scheme-elements-transitions, ($transition:$transitions)) !global;

    @include _theme-transition() {
      transition: $transitions;
    }
  }
}

/// Ajout d'une couleur de background sur un Ã©lÃ©ment
/// @access public
/// @param {String}  $set-name - nom de la combinaison comme dÃ©fini dans settings/sets
/// @param {Bool | String} $transition - si true, une transition de couleur est appliquÃ©e sur la propriÃ©tÃ©
/// @param {Bool | String} $hover - si true, un effet de hover est appliquÃ© sur l'Ã©lÃ©ment en correspondance avec la couleur. la valeur override permet de simplement changer la couleur de hover (si un hover a dÃ©ja Ã©tÃ© appliquÃ© sur l'Ã©lÃ©ment)
