/// @param {String}  $set-name - nom de la combinaison comme dÃ©fini dans settings/sets
/// @param {Bool | String}  $transition - si true, une transition de couleur est appliquÃ©e sur la propriÃ©tÃ©
/// @param {String} $side - le cÃ´tÃ© affectÃ©. valeurs: top, right, bottom, left
/// @param {List} $value - dÃ©finition des propriÃ©tÃ©s de border
@mixin scheme-element-border-color($set-name, $transition:false, $side:null, $value:1px solid $COLOR) {
  $prop:border;
  @if $side != null {
    $prop:border-#{$side};
  }
  @include _scheme-element($set-name, $prop, $transition, false, $value);
}

