@function _pow10($exponent) {
  @if $exponent == 0 {
    @return 1;
  }
  $result: 1;
  @for $i from 1 through $exponent {
    $result: $result * 10;
  }
  @return $result;
}

/// Fonction de transformation d'une string en nombre intÃ©gral
/// @access public
/// @param {String} $value - le nombre en chaÃ®ne de caractÃ¨res
@function str-to-num($value) {
  $length: str-length($value);
  $int: ();
  $float: ();
  $floating: false;
  $neg: false;

  @for $i from 1 through $length {
    $n: str-slice($value, $i, $i);

    @if $n == '.' {
      $floating: true;
    }
    @else if $n == '-' {
      $neg: true;
    }
    @else {
      @for $d from 0 through 9 {
        @if quote($d) == $n {
          @if $floating {
            $float: append($float, $d);
          }
          @else {
            $int: append($int, $d);
          }
        }
      }
    }
  }

  $result: 0;

  $length: length($int);
  @if $length > 0 {
    @for $i from 1 through $length {
      $result: $result + (nth($int, $i) * _pow10($length - $i));
    }
  }

  $length: length($float);
  @if $length > 0 {
    @for $i from 1 through $length {
      $result: $result + (nth($float, $i) / _pow10($i));
    }
  }

  @if $neg {
    @return -$result;
  }
  @else {
    @return $result;
  }
}

/// Remplacement dans une chaine de caractÃ¨res
/// @access public
/// @param {String} $stack - chaine de caractÃ¨re oÃ¹ chercher
/// @param {String} $needle - Ã©lÃ©ment Ã  chercher
/// @param {String} $content - Ã©lÃ©ment de remplacement
/// @return {String} la chaÃ®ne de caractÃ¨res transformÃ©e
@function str-replace($stack, $needle, $content) {
  $is:quote($stack);
  $in:quote($needle);
  $ic:quote($content);

  $i:str-index($is, $in);

  @while $i != null {
    $is:unquote(str_slice($is, 1, $i - 1) + $ic + str_slice($is, $i + str-length($in), str_length($is)));
    $i:str-index($is, $in);
  }

  @return unquote($is);
}

/// DÃ©coupage d'une chaine de caractÃ¨res avec un sÃ©parateur particulier
/// @access public
/// @param {String} $string - chaine de caractÃ¨re Ã  dÃ©couper
/// @param {String} $separator - sÃ©parateur
@function str-split($string, $separator:',') {
  $tmp: str-slice($string, 1);
  $list: ();
  $index: str-index($string, $separator);

  @if $index == null {
    $list: ($string);
  }
  @else {
    $list: join((str-slice($string, 1, $index - 1)), str-split(str-slice($string, $index + 1), $separator));
  }
  @return $list;
}
